import {GetActionsType, StateType} from "../store";

//================ TYPE ==================
export type InitialStateType = typeof initialState;
export type AppActionsType = GetActionsType<typeof appAC>
//type ThunkType = BaseThunkType<AppActionsType>
export type LangType = "RU" | "ENG" | "GE"
export interface IProduct {
    src: string
    title: string
    price: string
    description: string
}


//================ INITIAL ==================
const initialState = {
    lang: "RU" as LangType,
    burgerOpen: false,
    product: null as null | IProduct,
    showModalForm: false,
    showModalProduct: false,
    showModalThanks: false,

};

//================ REDUCER ===================
export const appReducer = (state = initialState, action: AppActionsType): InitialStateType => {
    switch (action.type) {
        case 'BARBER/APP/SET_LANG': {
            return {...state, lang: action.lang}
        }
        case 'BARBER/APP/SET_BURGER_OPEN': {
            return {...state, burgerOpen: action.burgerOpen}
        }
        case 'BARBER/APP/SET_PRODUCT': {
            return {...state, product: action.product}
        }
        case 'BARBER/APP/SET_SHOW_MODAL_FORM': {
            return {...state, showModalForm: action.showModalForm}
        }
        case 'BARBER/APP/SET_SHOW_MODAL_PRODUCT': {
            return {...state, showModalProduct: action.showModalProduct}
        }
        case 'BARBER/APP/SET_SHOW_MODAL_THANKS': {
            return {...state, showModalThanks: action.showModalThanks}
        }
        default:
            return state;
    }
};

// ================ ACTION CREATORS =================
export const appAC = {
    setLang: (lang: LangType) => ({type: 'BARBER/APP/SET_LANG', lang} as const),
    setBurgerOpen: (burgerOpen: boolean) => ({type: 'BARBER/APP/SET_BURGER_OPEN', burgerOpen} as const),
    setProduct: (product: IProduct) => ({type: 'BARBER/APP/SET_PRODUCT', product} as const),
    setShowModalForm: (showModalForm: boolean) => ({type: 'BARBER/APP/SET_SHOW_MODAL_FORM', showModalForm} as const),
    setShowModalProduct: (showModalProduct: boolean) => ({type: 'BARBER/APP/SET_SHOW_MODAL_PRODUCT', showModalProduct} as const),
    setShowModalThanks: (showModalThanks: boolean) => ({type: 'BARBER/APP/SET_SHOW_MODAL_THANKS', showModalThanks} as const),
}
//================ THUNK CREATORS ==================

//============== SELECTORS =================
export const selectLang = (state: StateType) => state.app.lang;
export const selectBurgerOpen = (state: StateType) => state.app.burgerOpen;
export const selectProduct = (state: StateType) => state.app.product;

export const selectShowModalForm = (state: StateType) => state.app.showModalForm;
export const selectShowModalProduct = (state: StateType) => state.app.showModalProduct;
export const selectShowModalThanks = (state: StateType) => state.app.showModalThanks;

