import React, {FC} from 'react';
import src from '../../assets/img/products/product-7-big.png';
import {selectLang} from "../../store/reducers/app.reducer";
import {useSelector} from "react-redux";
import {translate} from "../../helpers/lang";
import axios from "axios";



export const ProductUs = () => {
    const lang = useSelector(selectLang);
    const title = translate("ЗАГОЛОВОК", lang);
    const text = translate("Барбершоп  для тех, кто ценит хороший сервис, качество стрижки и серьезное отношение. Для всех, кому важен опрятный внешний вид бороды и волос, кто всегда хочет быть, как «с иголочки», у нас есть все необходимое для осуществления подобных желаний.", lang);
    const buttonLabel = translate("О товаре", lang);

    const onClick = async () => {
           try {
               const instance = axios.create({
                   baseURL: 'https://api.unisender.com/en/api/',
                   withCredentials: true,
               });
               const KEY = "6p4th9gf6z66cfqutkx3inz484ohmsc3rbcwx7cy";
               const email = 'alexaltrex@mail.ru';
               const senderName = 'sender name';
               const senderEmail = 'test@mail.ru';
               const subject = 'subject';

               //'https://api.unisender.com/en/api/subscribe?format=json&api_key=6p4th9gf6z66cfqutkx3inz484ohmsc3rbcwx7cy&list_ids=1&fields[email]=alexaltrex@mail.ru&double_optin=0&overwrite=0'

               //const response = await instance.get(`sendEmail?format=json&api_key=${KEY}&email=${email}&sender_name=${senderName}&sender_email=${senderEmail}&subject=${subject}`);
               const response = await instance.post(`subscribe?format=json&api_key=${KEY}&list_ids=1&fields[email]=${email}&double_optin=0&overwrite=0`);
           } catch (e: any) {
               console.log(e.message)
           }
    }

    return (
        <div className="product-us">
            <div className="product-us-bg" />

            <div className="container">
                <div className="product-us-content">
                    <div className="product-us-content_desc">
                        <h2>{title}</h2>
                        <p>{text}</p>
                    </div>

                    <div className="product-us-content_product">
                        <img src={src} alt=""/>
                        <button onClick={onClick}>
                            {buttonLabel}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

