import React from "react";
import {Form, IFormValues} from "../../Form/Form";
import {useDispatch} from "react-redux";
import {appAC} from "../../../store/reducers/app.reducer";

export const ContentForm = () => {
    const dispatch = useDispatch();
    const onClose = () => dispatch(appAC.setShowModalForm(false));
    const onSubmitHandler = (values: IFormValues) => {
        console.log(values);
        dispatch(appAC.setShowModalThanks(true));
        onClose();
    }

    return (
        <div className="modal-form">
            <div className="modal-abstract-close"
                 onClick={onClose}>
                X
            </div>
            <div className="modal-form-bg"/>
            <Form onSubmitHandler={onSubmitHandler}/>
        </div>
    )
}