import React from 'react';
import {Menu} from "../Menu/Menu";
import {MobileMenu} from "../MenuMobile/MenuMobile";
import mobileBurger from '../../assets/img/mobile-burger.svg';
import {translate} from "../../helpers/lang";
import {useDispatch, useSelector} from "react-redux";
import {appAC, selectBurgerOpen, selectLang} from "../../store/reducers/app.reducer";

export const Header = () => {
    const burgerOpen = useSelector(selectBurgerOpen);
    const dispatch = useDispatch();
    const onClickHandler = () => dispatch(appAC.setBurgerOpen(!burgerOpen))
    const onShowModalForm = () => dispatch(appAC.setShowModalForm(true));


    const lang = useSelector(selectLang);

    const subtitle = translate("Добро пожаловать!", lang);
    const title1 = translate("Твой новый", lang);
    const title2 = translate("безупречный стиль!", lang);
    const buttonLabel = translate("Оставить заявку", lang);



    return (
        <div className="header" id="header">
            <MobileMenu/>
            <div className="header-burger"
                 style={{display: burgerOpen ? 'none' : 'block'}}
                 onClick={onClickHandler}
            >
                <img src={mobileBurger} alt="" />
            </div>
            <div className="header-bg-black" />
            <div className="container">
                <div className="header-content">
                    <Menu />
                    <h2>{subtitle}</h2>
                    <h1>
                        <p>{title1}</p>
                        <p>{title2}</p>
                    </h1>
                    <button onClick={onShowModalForm}>{buttonLabel}</button>
                </div>
            </div>
        </div>
    );
};

