import {useDispatch, useSelector} from "react-redux";
import {appAC, selectLang, selectProduct} from "../../../store/reducers/app.reducer";
import React from "react";
import {translate} from "../../../helpers/lang";

export const ContentProduct = () => {
    const dispatch = useDispatch();
    const onClose = () => dispatch(appAC.setShowModalProduct(false));
    const product = useSelector(selectProduct);
    const lang = useSelector(selectLang);

    const onBuyHandler = () => {
        onClose();
        dispatch(appAC.setShowModalForm(true))
    }

    return (
        <div className="modal-product">

            <div className="modal-abstract-close"
                 onClick={onClose}>
                X
            </div>

            <div className="modal-line" style={{top: 40}}/>
            <div className="modal-line" style={{bottom: 30}}/>

            {
                product &&
                <div className="modal-product_item">
                    <div className="modal-product_item-img">
                        <img src={product.src} alt=""/>
                    </div>

                    <div className="modal-product_item-desc">
                        <h2>{translate(product.title, lang)}</h2>
                        <h3>{`${translate('Цена', lang)}: ${product.price} ₽`}</h3>
                        <p>{translate('О товаре', lang)}</p>
                        <span>{translate(product.description, lang)}</span>
                    </div>
                </div>
            }

            <button onClick={onBuyHandler}
            >
                {translate("Купить", lang)}
            </button>
        </div>
    )
}