import React, {FC, useState} from "react";
import {useSelector} from "react-redux";
import {selectLang} from "../../store/reducers/app.reducer";
import {translate} from "../../helpers/lang";
import {useFormik} from "formik";

export interface IFormValues {
    name: string
    email: string
    phone: string
}
type ErrorsType = Partial<IFormValues>
const regEmail = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;

interface IForm {
    onSubmitHandler: (values: IFormValues) => void
}

export const Form: FC<IForm> = ({onSubmitHandler}) => {
    const [checked, setChecked] = useState(true);
    const lang = useSelector(selectLang);
    const formTitle = translate("Оставте заявку", lang);
    const formSubtitle = translate("Мы ответим вам!", lang);
    const namePlaceholder = translate("Ваше Имя", lang);
    const emailPlaceholder = translate("E-mail", lang);
    const phonePlaceholder = translate("Ваш телефон", lang);
    const buttonLabel = translate("Отправить заявку", lang);
    const checkboxLabel = translate("Согласен (на) с условиями Пользовательского соглашения", lang);
    const requiredLabel = translate("Заполните поле", lang);
    const wrongLabel = translate("Неверный формат", lang);

    const initialValues: IFormValues = {
        name: '',
        email: '',
        phone: '',
    }

    const validate = ({name, email, phone}: IFormValues): ErrorsType => {
        const errors = {} as ErrorsType

        if (!name) {
            errors.name = requiredLabel
        }

        if (!email) {
            errors.email = requiredLabel
        }

        if (!phone) {
            errors.phone = requiredLabel
        }

        if (email && !regEmail.test(email)) {
            errors.email = wrongLabel
        }

        return errors
    }

    const onSubmit = (values: IFormValues) => {
        onSubmitHandler(values);
    }

    const formik = useFormik({
        initialValues,
        validate,
        onSubmit,
    });

    return (
        <div className="contact-us_content-form">
            <form onSubmit={formik.handleSubmit}>
                <h4>{formTitle}</h4>
                <p>{formSubtitle}</p>

                <div style={{
                    width: "100%",
                    position: "relative"
                }}>
                    <input type="text"
                           placeholder={namePlaceholder}
                           {...formik.getFieldProps("name")}
                    />
                    {formik.errors.name && <p style={{
                        position: "absolute",
                        left: 0,
                        bottom: 0,
                        margin: 0,
                        fontSize: 12,
                        color: "red",
                        paddingLeft: "10px"
                    }}>{formik.errors.name}</p>}
                </div>

                <div style={{
                    width: "100%",
                    position: "relative"
                }}>
                    <input type="text"
                           placeholder={emailPlaceholder}
                           {...formik.getFieldProps("email")}
                    />
                    {formik.errors.email && <p style={{
                        position: "absolute",
                        left: 0,
                        bottom: 0,
                        margin: 0,
                        fontSize: 12,
                        color: "red",
                        paddingLeft: "10px"
                    }}>{formik.errors.email}</p>}
                </div>

                <div style={{
                    width: "100%",
                    position: "relative"
                }}>
                    <input type="text"
                           placeholder={phonePlaceholder}
                           {...formik.getFieldProps("phone")}
                    />
                    {formik.errors.phone && <p style={{
                        position: "absolute",
                        left: 0,
                        bottom: 0,
                        margin: 0,
                        fontSize: 12,
                        color: "red",
                        paddingLeft: "10px"
                    }}>{formik.errors.phone}</p>}
                </div>

                <button type="submit">{buttonLabel}</button>

                <div className="contact-us_content-form-check" onClick={() => setChecked(!checked)}>
                    <div>
                        <input type="checkbox" checked={checked}/>
                        <span className="checkmark"/>
                    </div>
                    <span style={{userSelect: "none"}}>{checkboxLabel}</span>
                </div>
            </form>
        </div>
    )
}