import React, {useState} from 'react';
import './App.css';
import {Header} from "./components/A0_Header_Home/Header";
import {Products} from "./components/A1_Products/Products";
import {ProductUs} from "./components/A2_ProductUs/ProductUs";
import {AboutUs} from "./components/A3_AboutUs/AboutUs";
import {Reviews} from "./components/A4_Reviews/Reviews";
import {ContactUs} from "./components/A5_ContactUs/ContactUs";
import {Footer} from "./components/A6_ Footer/Footer";
import {useDispatch, useSelector} from "react-redux";
import {
    appAC,
    selectBurgerOpen,
    selectShowModalForm,
    selectShowModalProduct, selectShowModalThanks
} from "./store/reducers/app.reducer";
import {Modal} from "./components/Modal/Modal";
import {ContentForm} from "./components/Modal/ContentForm/ContentForm";
import {ContentProduct} from "./components/Modal/ContentProduct/ContentProduct";
import {ContentThanks} from "./components/Modal/ContentThanks/ContentThanks";


export const App = () => {
    const dispatch = useDispatch();
    const burgerOpen = useSelector(selectBurgerOpen);

    const showModalForm = useSelector(selectShowModalForm);
    const showModalProduct = useSelector(selectShowModalProduct);
    const showModalThanks = useSelector(selectShowModalThanks);

    const [isOpenModal, setIsOpenModal] = useState(false);
    const [type, setType] = useState('product');

    const setIsOpenModalHandler = (value: boolean) => {
        setType("form");
        setIsOpenModal(value);
    };

    return (
        <div className="landing"
             style={
                 (burgerOpen || showModalForm || showModalProduct || showModalThanks)
                     ? {
                         height: "100vh",
                         overflow: "hidden"
                     } : {}
             }
        >

            {
                showModalForm &&
                <Modal onClose={() => dispatch(appAC.setShowModalForm(false))}>
                    <ContentForm/>
                </Modal>
            }

            {
                showModalThanks &&
                <Modal onClose={() => dispatch(appAC.setShowModalThanks(false))}>
                    <ContentThanks/>
                </Modal>
            }

            {
                showModalProduct &&
                <Modal onClose={() => dispatch(appAC.setShowModalProduct(false))}>
                    <ContentProduct/>
                </Modal>
            }

            <Header/>
            <Products/>
            <ProductUs/>
            <AboutUs/>
            <Reviews/>
            <ContactUs/>
            <Footer/>
        </div>
    );
};

