import React, {FC} from 'react';
import bgMobileProducts from '../../assets/img/products/bg-mobile-products.svg';
import {data} from "./data";
import {useDispatch, useSelector} from "react-redux";
import {appAC, IProduct, selectLang} from "../../store/reducers/app.reducer";
import {translate} from "../../helpers/lang";



export const Products = () => {
    const lang = useSelector(selectLang);
    const buttonLabel = translate("Подробнее", lang);



    const dispatch = useDispatch();

    const onProductOpen = (product: IProduct) => {
        dispatch(appAC.setProduct(product));
        dispatch(appAC.setShowModalProduct(true));
    }

    return (
        <div className="product" id="products">
            <div className="container">
                <img src={bgMobileProducts} alt="" className="product-bg-mobile"/>
                <img src={bgMobileProducts} alt="" className="product-bg-mobile" style={{bottom: -88, top: 'unset'}}/>

                {
                    data.map(({category, products}, i) => (
                        <div className="product-content" key={i}>
                            <h2>{translate(category, lang)}</h2>
                            <div className="product-content_items">
                                {
                                    products.map((product, j) => (
                                        <div className="product-content_items--item" key={j}>
                                            <img src={product.src} alt=""/>
                                            <h3>{translate(product.title, lang)}</h3>
                                            <p>{`${product.price} ₽`}</p>
                                            <button onClick={() => onProductOpen(product)}>
                                                {translate(buttonLabel, lang)}
                                            </button>
                                        </div>
                                    ))
                                }
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    );
};

