import React from 'react';
import {useDispatch, useSelector} from "react-redux";
import {appAC, selectLang} from "../../store/reducers/app.reducer";
import {translate} from "../../helpers/lang";
import {Form, IFormValues} from "../Form/Form";

export const ContactUs = () => {
    const lang = useSelector(selectLang);
    const title = translate("У ВАС ОСТАЛИСЬ ВОПРОСЫ?", lang);
    const subtitle = translate("ЗАПОЛНИТЕ ПОЛЯ И МЫ ОТВЕТИМ ВАМ!", lang);
    const dispatch = useDispatch();

    const onSubmitHandler = (values: IFormValues) => {
        console.log(values);
        dispatch(appAC.setShowModalThanks(true))
    }

    return (
        <div className="contact-us" id="contacts">
            <div className="product-us-bg"/>
            <div className="container">
                <div className="contact-us_content">
                    <div className="contact-us_content-desc">
                        <h2>{title}</h2>
                        <p>{subtitle}</p>
                    </div>
                    <Form onSubmitHandler={onSubmitHandler}/>
                </div>
            </div>
        </div>
    );
};

