import React from 'react';
import line from '../../assets/img/line.svg';
import lg from '../../assets/img/lg.svg';
import {useSelector} from "react-redux";
import {selectLang} from "../../store/reducers/app.reducer";
import {translate} from "../../helpers/lang";

export const AboutUs = () => {
    const lang = useSelector(selectLang);
    const subtitle = translate("О нас", lang);
    const description = translate("Барбершоп  для тех, кто ценит хороший сервис, качество стрижки и серьезное отношение. Для всех, кому важен опрятный внешний вид бороды и волос, кто всегда хочет быть, как «с иголочки», у нас есть все необходимое для осуществления подобных желаний.", lang);

    return (
        <div className="about-us" id={'about'}>
            <img src={line} alt="" className='line-bg-img'/>
            <div className="about-us-bg" />
            <div className="container">
                <div className="about-us_content">
                    <div className="about-us_content-desc">
                        <img src={lg} alt="" className={'hidden-desktop'}/>
                        <h2>{subtitle}</h2>
                        <p className={'hidden-mobile'}>{description}</p>
                    </div>

                    <p className={'hidden-desktop'}>{description}</p>
                    <img src={lg} alt="" className={'hidden-mobile'}/>
                </div>
            </div>
        </div>
    );
};

