import React from "react";
import style from "./LangSwitcher.module.scss"
import {useDispatch, useSelector} from "react-redux";
import {appAC, LangType, selectLang} from "../../store/reducers/app.reducer";
import clsx from "clsx";

export const LangSwitcher = () => {
    const currentLang = useSelector(selectLang);
    const langs = ["GE", "ENG", "RU"];
    const dispatch = useDispatch();
    const onClickHandler = (lang: LangType) => dispatch(appAC.setLang(lang))
    return (
        <div className={style.langSwitcher}>
            {
                langs.map((lang, index) => (
                    <button key={index}
                            className={clsx({
                                [style.btn]: true,
                                [style.btn_selected]: lang === currentLang,
                            })}
                            onClick={() => onClickHandler(lang as LangType)}
                    >
                        <p>{lang}</p>
                    </button>
                ))
            }
            <div className={clsx({
                [style.mask]: true,
                [style.mask_eng]: currentLang === "ENG",
                [style.mask_ru]: currentLang === "RU",
            })}/>
        </div>
    )
}