import React from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import photo from '../../assets/img/photo.png';
import {selectLang} from "../../store/reducers/app.reducer";
import {useSelector} from "react-redux";
import {translate} from "../../helpers/lang";

const slides = [
    {src: photo, name: "Александр Зайчиков 1", text: "Дорогие друзья, сходил я на этот ваш барбершоп. Думал обычное парикмахерское, но был не прав. Мне все понравилось."},
    {src: photo, name: "Александр Зайчиков 2", text: "Дорогие друзья, сходил я на этот ваш барбершоп. Думал обычное парикмахерское, но был не прав. Мне все понравилось."},
    {src: photo, name: "Александр Зайчиков 3", text: "Дорогие друзья, сходил я на этот ваш барбершоп. Думал обычное парикмахерское, но был не прав. Мне все понравилось."},
    {src: photo, name: "Александр Зайчиков 4", text: "Дорогие друзья, сходил я на этот ваш барбершоп. Думал обычное парикмахерское, но был не прав. Мне все понравилось."},
];

export const Reviews = () => {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };
    const lang = useSelector(selectLang);
    const subtitle = translate("Отзывы Клиентов", lang);


    return (
        <div className="reviews" id={'reviews'}>
            <div className="container">
                <div className="reviews-content">
                    <h2>{subtitle}</h2>

                    <Slider {...settings}>
                        {
                            slides.map(({src, name, text}, index) => (
                                <div key={index} className="slide">
                                    <img src={src} alt=""/>
                                    <div className="slide-desc">
                                        <h4>{translate(name, lang)}</h4>
                                        <p>{translate(text, lang)}</p>
                                    </div>
                                </div>
                            ))
                        }
                    </Slider>
                </div>
            </div>
        </div>
    );
};

