import {LangType} from "../store/reducers/app.reducer";

export const  dictionary = {
   "Товары": [
       "Products",
       "արտադրանք",
   ],
};

export const translate = (str: string, lang: LangType):string => {
    if (lang === "RU") {
        return str
    } else {
        if (dictionary.hasOwnProperty(str)) {
            if (lang === "ENG") {
                // @ts-ignore
                return dictionary[str][0];
            } else {
                // @ts-ignore
                return dictionary[str][1];
            }
        } else {
            if (lang === "ENG") {
                // @ts-ignore
                return "по-английски ???";
            } else {
                // @ts-ignore
                return "по-армянски ???";
            }
        }
    }
}
