import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {appAC, selectLang} from "../../../store/reducers/app.reducer";
import {translate} from "../../../helpers/lang";
import {svgIcons} from "../../../assets/svg/svgIcons";

export const ContentThanks = () => {
    const dispatch = useDispatch();
    const onClose = () => dispatch(appAC.setShowModalThanks(false));

    const lang = useSelector(selectLang);
    const title1 = translate("СПАСИБО ЗА ЗАЯВКУ", lang);
    const title2 = translate("МЫ ВАМ ПОЗВОНИМ В ТЕЧЕНИИ НЕСКОЛЬКИХ МИНУТ", lang);
    const link = translate("Перейти на главную", lang);

    return (
        <div className="modal-finish">
            <div className="modal-abstract-close" onClick={onClose}>
                X
            </div>
            <div className="modal-line" style={{top: 30}}/>
            <div className="modal-line" style={{bottom: 27}}/>
            <div className="modal-form-bg"/>
            <div className="modal-finish_content">
                <h2>
                    <p>{title1}</p>
                    <p>{title2}</p>
                </h2>

                <div className="modal-finish_content-button"
                     onClick={onClose}
                >
                    <p>{link}</p>
                    <div>{svgIcons.arrowUp}</div>
                </div>
            </div>
        </div>
    )
}