import React, {FC} from 'react';
import mobileBurger from '../../assets/img/mobile-burger.svg';
import instagram from '../../assets/img/instagram.svg';
import facebook from '../../assets/img/facebook.svg';
import {svgIcons} from "../../assets/svg/svgIcons";
import {translate} from "../../helpers/lang";
import {headerLinks} from "../Menu/Menu";
import {appAC, LangType, selectBurgerOpen, selectLang} from "../../store/reducers/app.reducer";
import {useDispatch, useSelector} from "react-redux";
import style from './MenuMobile.module.scss'
import clsx from "clsx";

export const MobileMenu = () => {
    const lang = useSelector(selectLang);
    const burgerOpen = useSelector(selectBurgerOpen);
    const dispatch = useDispatch();
    const onClickHandler = () => dispatch(appAC.setBurgerOpen(!burgerOpen));
    const setLang = (l: LangType) => {
        dispatch(appAC.setLang(l));
    };

    return (
        <div className='menu-mobile' style={{display: burgerOpen ? 'block' : 'none'}}>
            <div className="menu-mobile_header">
                <div>
                    {svgIcons.logoInBurger}
                </div>
                <img src={mobileBurger} alt="" onClick={onClickHandler}/>
            </div>

            <div className="menu-mobile_desc">
                {
                    headerLinks.map(({href, label}, index) => (
                        <a key={index}
                           href={href}
                           onClick={onClickHandler}
                        >
                            {translate(label, lang)}
                        </a>
                    ))
                }
                <div className={style.socialLinks}>
                    <a href="#" className={style.link}>
                        {svgIcons.instagram}
                    </a>
                    <a href="#" className={style.link}>
                        {svgIcons.facebook}
                    </a>
                </div>

                <div className={style.langs}>
                    {
                        ['GE', 'ENG', 'RU'].map((l, index) => (
                            <button className={clsx({
                                [style.btn]: true,
                                [style.btn_selected]: l === lang,
                            })}
                                    onClick={() => setLang(l as LangType)}
                            >
                                {l}
                            </button>
                        ))
                    }
                </div>
            </div>
        </div>
    );
};

